body{
    display: flex;
    flex-direction: column;
    justify-content: center;
}
.container{
    max-width: 1464px !important;
    width: 100%;
}  
.template-title{
    font-weight: 700;
    font-size: 40px;
    line-height: 150%;
    color: #333;
}
.secondary-title{
    font-weight: 500;
    font-size: 24px;
    color: #000; 
}
.description{
    font-weight: 400;
    font-size: 20px;
    line-height: 150%;
    color: #666;
}
.gradient-letter{
    background: linear-gradient(45deg, #8080AF 33%, #9F709F 66%);
    -webkit-background-clip: text;
    -webkit-text-fill-color: transparent;}
.btn-custom{
    font-size: 12px;
    width: 97px;
    height: 39px;
    border-radius: 6px;
    background-color: transparent;
    border: 1px solid #2a274080;
    color: #2a2740;
    &:hover{
        border-color: #ab79a2;
        color: #ab79a2;
    }
    &:active{
        border-color: #8680B2;
        color: #8680B2;
    }
    cursor: auto;
}
.crypto-1,
.crypto-2,
.crypto-3{
    position: absolute;
    top: 0;
    left: 0;
    .secondary-title{
        font-size: 9px;
        font-weight: 700;
        color: #2a2740;
    }
    .exchange-rate{
        font-size: 15px;
        font-weight: 700;
        color: #2a2740;
    }
}
.feat-info,
.feat-info_2,
.feat-info_3{
    .ellipse{
        margin-right: 24px;
        background: #F2ECFE;
        padding: 14px;
        border-radius: 45%;
        margin-bottom: 16px;
        }
}

